/* Everything: no margins padding & box-sizing : border-box & default white colored text */
* {
  margin: 0;
  padding: 0;
  padding-bottom: 10px;
  box-sizing: border-box;
  color: white;
  text-align:center;
  line-height: 10vh;
}

/* Background is black by default */
body {
  background: black;
}